


import "./index.scss";
import {Button,Table} from "antd"


export default class Role extends Component{
    state = {
        loading:false
    }
    render(){
        const columns=[
            {
                title: '角色名称',
                dataIndex: 'rolename',
                key: 'rolename',
              },
              {
                title: '创建时间',
                dataIndex: 'creat_time',
                key: 'creat_time',
              },
              {
                title: '授权时间',
                dataIndex: 'pri_time',
                key: 'pri_time',
              },
              {
                title: '授权人',
                dataIndex: 'pri_name',
                key: 'pri_name',
              },
              {
                title: '操作',
                dataIndex: 'action',
                render: () => (
                    <span>
                      <Button type="link">设置权限</Button>
                    </span>
                  ),
              },
        ]
        const data = [
            {
                key: '1',
                rolename: 'John Brown',
                pri_name: "admin",
                pri_time: "2019-9-22",
                creat_time: "2019-9-22",
              },
              {
                key: '2',
                rolename: 'John Brown',
                pri_name: "admin",
                pri_time: "2019-9-22",
                creat_time: "2019-9-22",
              },
        ]
        const {loading} = this.state
        return(
            <div className="role">
                <div className="content">
                <div className="contentTop">
						<Button type="primary" onClick={this.addUser} style={{marginRight:20}}>
							创建角色
						</Button>
					</div>
					<div className="contentTable">
						<Table
							columns={columns}
							loading={loading}
							bordered
							dataSource={data}
							pagination={{ defaultPageSize: 6, showQuickJumper: true }}
							rowKey="key"
						/>
					</div>
                </div>
            </div>
        )
    }
}